import React, { Component } from 'react';
import axios from 'axios';
import PokemonCard from './PokemonCard';

export default class PokemonList extends Component {
    constructor(props) {
        super(props);
      }

    state = {
        url: 'https://pokeapi.co/api/v2/pokemon?limit=100',
        pokemon: null
    };
    

    async componentDidMount(){
        const res = await axios.get(this.state.url);
        const detailsReponse = await Promise.all(res.data['results'].map(details => axios.get(details.url)))
        this.setState({ pokemon: detailsReponse.map(el => el.data) })
    }

    
    render() {
        return (
            <React.Fragment>
                {this.state.pokemon ? (
                    <div className='row'>
                        {this.state.pokemon.filter(pokemon => pokemon.types[0].type.name === this.props.filter 
                        || this.props.filter == null 
                        || (pokemon.types.length > 1 && pokemon.types[1].type.name === this.props.filter)
                        ).map((pokemon) => (
                            <PokemonCard 
                                key={pokemon.name}
                                pokemonDetails={pokemon}
                            />
                            
                        ))}
                        
                    </div>
                ) : (
                    <h1>Loading Pokemon</h1>
                )}
            </React.Fragment>
        );
    }
}