import React, { Component } from 'react';
import { Button } from 'react-bootstrap';

export default class NavBar extends Component {
    
  constructor(props) {
      super(props);
      this.state = {value: 'null'};
  
      this.handleChange = this.handleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }
  
    handleChange(event) {
      this.setState({value: event.target.value});
    }
  
    handleSubmit(event) {
      this.props.handleFilterChange(this.state.value)
      event.preventDefault();
    }
  
    render() {
      return (
          <div>
            <nav className='navbar navbar-expand-md navbar-dark bg-primary fixed-top'>
              <a href='' className='navbar-brand col-sm-3 col-md-2 mr-0 align-items-center'>
                  PokeDex
              </a>
              <form onSubmit={this.handleSubmit}>
                <select className='browser-default custom-select align-items-center' aria-label='.form-select-lg example' value={this.state.value} onChange={this.handleChange}>
                  <option value=''>Select type of pokemon</option>
                  <option value='bug'>Bug</option>
                  <option value='dark'>Dark</option>
                  <option value='dragon'>Dragon</option>
                  <option value='electric'>Electric</option>
                  <option value='fairy'>Fairy</option>
                  <option value='fighting'>Fighting</option>
                  <option value='fire'>Fire</option>
                  <option value='flying'>Flying</option>
                  <option value='ghost'>Ghost</option>
                  <option value='grass'>Grass</option>
                  <option value='ground'>Ground</option>
                  <option value='ice'>Ice</option>
                  <option value='normal'>Normal</option>
                  <option value='poison'>Poison</option>
                  <option value='psychic'>Psychic</option>
                  <option value='rock'>Rock</option>
                  <option value='steel'>Steel</option>
                  <option value='water'>Water</option>
                </select>
                <Button variant="warning" type="submit"> Submit </Button>
              </form>
            </nav>  
          </div>
      )
  }
}
